.modal-container {
    width: 100%;
    height: 100vh;
    background: #000000c9;
    position: fixed;
    z-index: 99;
    top: 0;
}
.modal-container div {
    position: absolute;
    width: 700px;
    height: 300px;
    background: #fff;
    top: 50%;
    margin-top: -150px;
    left: 50%;
    margin-left: -350px;
    border-radius: 10px;
    box-shadow: 0px 0px 10px 1px #fff;
}

.modal-container div img {
    width: 300px;
    border-radius: 10px 0 0 10px;
}
.modal-container div p {
    color: #000;
    position: absolute;
    top: 0;
    width: 400px;
    left: 50%;
    margin-left: -50px;
}
