    float: left;
    width: auto;
}
footer div.credito h3 {
    float: left;
    margin-right: 10px;
    
}
footer div.credito a {
    text-decoration: none;
    color: #fff;
    margin-right: 8px;
}
.primero-f {
    margin-right: 8px;
}
.segundo-f {
    margin-left: 8px;
}

@media (max-width: 500px) {
    .creditos {
        width: 100%;
    }
    footer div.credito {
        flex-direction: column;
    }
    footer div.credito p {
        width: 350px;
        margin-top: 0;
    }
  }
  
